<script>
  import { mapMutations } from 'vuex'
  import axios from 'axios'
  export default {
    name: 'Registro',
    data: () => ({
      loading: false,
      primera_vez: true,
      eleccion_id: null,
      valid: true,
      niveles: [],
      nombre: null,
      nivel: null,
      correo: null,
      codigo: null,
      coalicion: null,
      temporal: null,
      folio: null,
      grupopolitico: [],
      grupopolitico_id: [],
      grupopolitico_nombres: [],
      nombre_completoRules: [
      v => !!v || 'Nombre completo es requerido',
      v => (v && v.length >= 2) || 'Nombre completo debe tener más de 2 caracteres',
      ],
      emailRules: [
      v => !!v || 'Correo electrónico es requerido',
      v => /.+@.+/.test(v) || 'Correo electrónico debe ser válido',
      ],
      codigoUGRules: [
      v => !!v || 'Código U de G es requerido',
      v => (v && v.length >= 5) || 'Código U de G debe tener más de 2 caracteres',
      v => /^\d*$/.test(v) || 'Solo números',
      ],
      coalicionRules: [
      v => !!v || 'Coalición es requerida',
      ],
      nivelRules: [
      v => !!v || 'Nivel es requerido',
      ],
      cards: [],
      /*cards: [
      { id: 'ced', src: require('../assets/CED.jpeg'), flex: 2 },
      { id: 'ade', src: require('../assets/ade_logo_FEU.png'), flex: 2 },
      { id: 'mismo', src: require('../assets/mismo.png'), flex: 2 },
      { id: 'libre', src: require('../assets/libre.png'), flex: 2 },
      { id: 'impulso', src: require('../assets/impulso.png'), flex: 2 },
      { id: 'indigo', src: require('../assets/INDIGO.jpg'), flex: 2 },
      { id: 'diferente', src: require('../assets/DIFERENTE.png'), flex: 2 },
      { id: 'xpresion', src: require('../assets/XPRESION.png'), flex: 2 },
      { id: 'cu', src: require('../assets/CU.jpg'), flex: 2 },
      ],*/
    }),
   /* watch: {
      grupo_politico: function(val) {
        if(this.coalicion.value === "si") {
          console.log(val)
          this.grupo_politico = val
        } else {
          if(this.grupo_politico.length <= 1) {
            console.log(val)
          } else {
            this.grupo_politico.pop()
          }
        }
      }
    }, */
    methods: {
      ...mapMutations(['ocultarLoad', 'mostrarLoad']),
      async getGruposPoliticos() {
        let grupos_politicos = await axios.get('https://elecciones.feu.mx/api/management/obtainpoliticalgroups')
        let gp = grupos_politicos.data.data;
        Object.keys(gp).forEach((element) => {
          //console.log( gp[element]);
          this.cards.push({id: gp[element].id, title: gp[element].nombre,src: require('../assets/'+gp[element].imagen+'')});
        });
        this.cards = this.cards.sort(function (a, b) {
            if (a.title > b.title) {
              return 1;
            }
            if (a.title < b.title) {
              return -1;
            }
            // a must be equal to b
            return 0;
          });

      },
      async getEleccionId() {
        let id_eleccion = await axios.get('https://elecciones.feu.mx/api/management/obtainactiveelection')
      //console.log(id_eleccion.data.data[0].eleccion_id)
      this.eleccion_id = id_eleccion.data.data[0].eleccion_id;
    },
    async getObtenerNiveles() {
      this.loading = true;
      let niveles = await axios.get('https://elecciones.feu.mx/api/management/obtainnivels')
      this.loading = false;
      //console.log(`Niveles: ${niveles.data} `)
      let lista_niveles = niveles.data.data;
      Object.keys(lista_niveles).forEach((element) => {
      this.niveles.push({value: lista_niveles[element].nivel, label: lista_niveles[element].nivel});
    });
    },
   /* validate () {
      this.$refs.form.validate()
    },*/
    async validate() {
      let valido = await this.$refs.form.validate();
      //console.log(valido);
      if(valido) {
        let nivel_id;
        if(this.nivel === "Preparatoria") {
          nivel_id = 1;
        } else {
          nivel_id = 2;
        }
        try {
          Object.keys(this.grupopolitico).forEach((element) => {
            if(typeof this.grupopolitico[element].id !== 'undefined') {
              this.grupopolitico_id.push(this.grupopolitico[element].id);
            }
            if(typeof this.grupopolitico[element].title !== 'undefined') {
              this.grupopolitico_nombres.push(this.grupopolitico[element].title);
            }
          });
          console.log(this.grupopolitico_nombres)
          this.mostrarLoad({titulo: 'Enviando datos, por favor espere'});
          this.$store.commit('setNombrePresidente',(this.nombre))
          let respuesta = await axios.post('https://elecciones.feu.mx/api/management/registerStep1', {
            nombre: this.nombre,
            nivel: nivel_id,
            correo: this.correo,
            codigo: this.codigo,
            grupopolitico: this.grupopolitico_id,
            eleccion_id: this.eleccion_id
          });
          //console.log(respuesta)
          if(!respuesta.data.response) {
            let upper = respuesta.data?.message
            this.$toast.open({
                message: upper,
                type: 'error',
                position: 'bottom-right',
                duration: 5000,
            });
            this.$router.push({ name: 'Registro02', params: { eleccion_id:this.eleccion_id, codigo: this.codigo, nivel: nivel_id} })
          } else {
            this.$router.push({ name: 'Registro02', params: { eleccion_id:this.eleccion_id, codigo: this.codigo, nivel: nivel_id} })
          }
        }
        catch (ex) {
          console.error('inner', ex.message);
        }
        finally {
          this.$refs.form.resetValidation();
          this.ocultarLoad();
          console.log('finally');
        }
      } else {
        //this.$refs.form.resetValidation()
        this.$toast.open({
            message: '¡LOS CAMPOS NO PUEDEN ESTAR VACIOS!',
            type: 'error',
            position: 'bottom-right',
            duration: 5000,
        });
      }
    },
    validarCoalicion(val) {
      if(this.coalicion == "si") {
        this.grupopolitico.push(val);
      } else {
        if(this.grupopolitico.length <= 1) {
            //console.log(val)
          } else {
            this.grupopolitico.pop();
          }
        }
      },
      validarCoalicionTarjetas() {
        if(this.grupopolitico.length > 1) {
          this.grupopolitico = [];
        }
      }
    },
    mounted() {
      this.getGruposPoliticos();
      this.getEleccionId();
      this.getObtenerNiveles();
    }
  };
</script>