    <v-row no-gutters>
      <v-col
        cols="12"
        sm="12"
        md="12"
      >
        <v-img
        :src="require('../../assets/logo_FEU_gray@2x.png')"
        class="my-3 mt-16"
        contain
        height="130"
        />
      </v-col>
      <v-col
        cols="12"
        sm="12"
        md="12"
      >
        <p class="font-weight-medium text-center text-h6 mt-5">
      Ingrese su usuario y contraseña
    </p>
      </v-col>
      <v-col
        cols="12"
        sm="12"
        md="12"
      >
      <v-row no-gutters>
      <v-col
        cols="4"
        sm="4"
        md="4"
      >
        
      </v-col>
      <v-col
        cols="4"
        sm="4"
        md="4"
      >
       <v-form
    ref="form"
    v-model="valid"
    lazy-validation
    class="mb-15"
  >
    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="Usuario"
      outlined
      required
      placeholder="Correo electrónico"
