<script>
export default {
  name: 'Index',
  data: () => ({
    valid: true,
      password: '',
      passwordRules: [
        v => !!v || 'Contraseña es requerida',
        v => (v && v.length >= 8) || 'Minímo 8 caracteres',
      ],
      email: '',
      emailRules: [
        v => !!v || 'Correo electrónico es requerido',
        v => /.+@.+\..+/.test(v) || 'Correo electrónico debe ser válido',
      ],
  }),
};
